.section5-modal {
    position: fixed;
    z-index: 6;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    background: rgba(0, 0, 0, 0.5);
    .modal-container {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        .video {
            width: 100%;
            max-width: 1000px;
            height: 580px;
            background: #fff;
            position: relative;
            z-index: 1;
            iframe {
                width: 100%;
                height: 100%;
            }
        }
        button.modal-close {
            position: absolute;
            z-index: 2;
            top: 40px;
            right: 40px;
            border: 0;
            background: none;
            i {
                color: #fff;
                font-size: 24px;
            }
        }
    }
}