#sub01ArchiveArtistProfilesDetail {
    width: 100%;
    margin-top: 128px;
    padding: 0 20px;
    #section1 {
        width: 100%;
        height: 600px;
        display: flex;
        background: #333;
        .container {
            width: 100%;
            max-width: 1200px;
            margin: auto;
            .thumbnail {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    width: calc(100% / 2);
                    height: 600px;
                    object-fit: cover;
                    padding: 80px;
                }
                .info {
                    ul {
                        padding-right: 20px;
                        li {
                            padding-bottom: 32px;
                            h2 {
                                font-size: 28px;
                                color: #fff;
                                font-weight: 600;
                            }
                            h3 {
                                font-size: 18px;
                                color: #fff;
                                font-weight: 600;
                            }
                            p {
                                color: #ccc;
                                line-height: 200%;
                                font-size: 16px;
                                text-align: justify;
                            }
                            a {
                                color: #fff;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                width: 120px;
                                height: 40px;
                                font-size: 12px;
                                font-weight: 600;
                                border-radius: 6px;
                                background: linear-gradient(to right, rgb(102, 113, 240), rgb(164, 116, 202), rgba(240, 119, 152));
                                transition: all 0.3s ease-in-out;
                                &:hover {
                                    opacity: 0.5;
                                }
                            }
                        }
                    }
                }
            }
        }
        @media all and (max-width: 1000px) {
            .container {
                .thumbnail {
                    img {
                        padding: 20px;
                        height: 400px;
                    }
                    .info {
                        ul {
                            li {
                                padding-bottom: 20px;
                                h2 {
                                    font-size: 24px;
                                }
                                h3 {
                                    font-size: 16px;
                                }
                                p {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }
        @media all and (max-width: 600px) {
            height: auto;
            .container {
                .thumbnail {
                    flex-direction: column;
                    img {
                        width: 100%;
                        height: auto;
                    }
                    .info {
                        padding: 20px;
                        ul {
                            li {
                                padding-bottom: 16px;
                                h2 {
                                    font-size: 20px;
                                }
                                h3 {
                                    font-size: 14px;
                                }
                                p {
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }   
    #section2 {
        width: 100%;
        padding-top: 100px;
        .container {
            width: 100%;
            max-width: 1200px;
            margin: auto;
            display: flex;
            .col {
                width: calc(100% / 2);
                ul {
                    li {
                        h2 {
                            font-size: 20px;
                            font-weight: 600;
                            padding-bottom: 16px;
                        }
                        p {
                            line-height: 200%;
                            white-space: pre-wrap;
                        }
                    }
                }
            }
        }
        @media all and (max-width: 600px) {
            padding-top: 40px;
            .container {
                flex-direction: column;
                .col {
                    width: 100%;
                    padding-bottom: 28px;
                }
            }
        }
    }
    #section3 {
        padding: 100px 0;
        text-align: center;
        a {
            display: inline-block;
            font-size: 12px;
            font-weight: 600;
            padding: 16px 32px;
            background: #333;
            color: #fff;
            border: 1px solid transparent;
            transition: all 0.3s;
            &:hover {
                border: 1px solid #333;
                background: none;
                color: #333;
            }
        }
    }
}