#wrap {
    .scrollTop {
        position: fixed;
        z-index: 5;
        bottom: 80px;
        right: 8px;
        button {
            border: 0;
            background: transparent;
            transform: rotate(90deg);
            color: #999;
            cursor: pointer;
            transition: all 0.3s;
            i {
                font-size: 24px;
                color: var(--grayscale-40);
                transition: all 0.3s ease-in-out;
            }
            span {
                font-size: 12px;
                font-weight: 600;
            }
            &:hover {
                background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                background-clip: text;
                background-size: cover;
                color: transparent;
            }
        }
    }
}