#wrap {
    #main {
        #section5 {
            width: 100%;
            .container {
                width: 100%;
                height: auto;
                padding: 160px 0;
                background: url('../../../../../public/images/main/section5-img-1.png') no-repeat 50% 50%;
                position: relative;
                .title {
                    width: 100%;
                    text-align: center;
                    position: relative;
                    z-index: 2;
                    button.modal-open {
                        width: 56px;
                        height: 56px;
                        border: 0;
                        border-radius: 50%;
                        background: #6671f0;
                        text-align: center;
                        box-shadow: 0 2px 10px 10px rgba(52, 82, 255, 0.2);
                        transition: all 0.3s;
                        i {
                            margin-left: 6px;
                            font-size: 40px;
                            color: #fff;
                            transition: all 0.3s;
                        }
                        &:hover {
                            box-shadow: 0 2px 10px 10px rgba(52, 82, 255, 0.6);
                        }
                    }
                    h2 {
                        font-size: 40px;
                        font-weight: 600;
                        color: #fff;
                        padding-top: 24px;
                        padding-bottom: 48px;
                        line-height: 160%;
                    }
                    p {
                        font-size: 15px;
                        font-weight: 400;
                        line-height: 200%;
                        color: #fff;
                    }
                }
                .overlay {
                    position: absolute;
                    z-index: 1;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba(0, 0, 0, 0.5);
                }
                @media all and (max-width: 780px) {
                    .title {
                        padding: 0 16px;
                        h2 {
                            font-size: 24px;
                            padding-bottom: 24px;
                        }
                        p {
                            font-size: 12px;
                        }
                    }
                }
                @media all and (max-width: 500px) {
                    .title {
                        padding: 0 16px;
                        button.modal-open {
                            width: 40px;
                            height: 40px;
                            i {
                                font-size: 28px;
                                margin-left: 4px;
                            }
                        }
                        h2 {
                            font-size: 18px;
                            padding-bottom: 24px;
                        }
                        p {
                            font-size: 12px;
                        }
                    }
                }
            }
            &.on {
                .container {
                    animation: section7 2s forwards ease-in-out;
                }
                @keyframes section7 {
                      0% {opacity: 0;}
                    100% {opacity: 1;}
                }
            }
        }
    }
}