#wrap {
    #main {
        #section8 {
            padding: 100px 0;
            border-bottom: 1px solid #ccc;
            .container {
                width: 100%;
                max-width: 1170px;
                margin: 0 auto;
                .title {
                    text-align: center;
                    h2 {
                        display: inline-block;
                        font-size: 14px;
                        font-weight: 600;
                        padding-bottom: 20px;
                        background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                        background-clip: text;
                        background-size: cover;
                        color: transparent;
                    }
                    h3 {
                        padding-bottom: 80px;
                        font-size: 32px;
                        font-weight: 600;
                        line-height: 140%;
                    }
                }
                .content {
                    ul {
                        display: flex;
                        flex-wrap: wrap;
                        li {
                            width: calc(25% - 32px);
                            margin: 0 16px;
                            .gap {
                                width: 100%;
                                height: 320px;
                                overflow: hidden;
                                img {
                                    width: 100%;
                                    object-fit: cover;
                                    padding-bottom: 40px;
                                    transition: all 0.3s;
                                }
                            }
                            a {
                                display: block;
                                padding-bottom: 20px;
                                transition: all 0.3s;
                                h3 {
                                    font-size: 16px;
                                    font-weight: 500;
                                    line-height: 180%;
                                }
                            }
                            &:hover {
                                img {
                                    transform: scale(1.2);
                                }
                                a {
                                    color: #6671f0;
                                }
                            }
                        }
                    }
                }
                @media all and (max-width: 780px) {
                    .content {
                        ul {
                            li {
                                width: calc(50% - 32px);
                                &:nth-child(1),
                                &:nth-child(2) {
                                    padding-bottom: 24px;
                                }
                            }
                        }
                    }
                }
                @media all and (max-width: 480px) {
                    .title {
                        h2 {
                            font-size: 12px;
                            padding-bottom: 16px;
                        }
                        h3 {
                            font-size: 20px;
                            line-height: 140%;
                            padding-bottom: 16px;
                        }
                        a {
                            font-size: 10px;
                            padding: 8px 16px;
                        }
                    }
                    .content {
                        ul {
                            li {
                                width: calc(100% - 32px);
                                &:nth-child(1),
                                &:nth-child(2),
                                &:nth-child(3) {
                                    padding-bottom: 16px;
                                }
                                img {
                                    padding-bottom: 24px;
                                }
                                a {
                                    padding-bottom: 8px;
                                }
                            }
                        }
                    }
                }
            }
            &.on {
                .title {
                    animation: section11Title 2s forwards ease-in-out;
                }
                @keyframes section11Title {
                      0% {opacity: 0;}
                    100% {opacity: 1;}
                }
                .content {
                    ul {
                        li {
                            &:nth-child(1){
                                animation: section11Nth1 1.3s forwards ease-in-out;
                            }
                            &:nth-child(2){
                                animation: section11Nth2 1.6s forwards ease-in-out;
                            }
                            &:nth-child(3){
                                animation: section11Nth3 1.9s forwards ease-in-out;
                            }
                            &:nth-child(4){
                                animation: section11Nth4 2.2s forwards ease-in-out;
                            }
                            @keyframes section11Nth1 {
                                  0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section11Nth2 {
                                  0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section11Nth3 {
                                  0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section11Nth4 {
                                  0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                        }
                    }
                }
            }
        }
    }
}