#sub05BoardNoticeDetail {
    #section1 {
        width: 100%;
        max-width: 1340px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 100px 20px;

            // 타이틀
            .title {
                width: 100%;
                border-top: 1px solid #777;
                padding: 20px 40px;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                h2 {
                    width: 60px;
                    color: #6671f0;
                    font-size: 16px;
                    font-weight: 500;
                }
                h3 {
                    width: calc(100% - 60px);
                    padding: 0 16px;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 160%;
                }
                @media all and (max-width: 600px) {
                    padding: 20px;
                    flex-direction: column;
                    align-items: flex-start;
                    h3 {
                        padding: 8px 0 0 0;
                    }
                }
            }

            // 내용
            .content {
                width: 100%;
                padding: 40px;
                background: #f9f9f9;
                p {
                    font-size: 14px;
                    color: #333;
                    line-height: 160%;
                    white-space: pre-wrap;
                    line-height: 160%;
                    margin-top: 16px;
                }
                span {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    padding-bottom: 16px;
                    strong {
                        color: #777;
                        margin-right: 16px;
                    }
                    em {
                        color: #777;
                    }
                }
                @media all and (max-width: 600px) {
                    span {
                        flex-direction: column;
                        align-items: flex-end;
                        strong {
                            margin-right: 0;
                            margin-bottom: 8px;
                        }
                    }
                }
            }

            // 목록
            .back-btn-box {
                width: 100%;
                padding-top: 80px;
                text-align: center;
                button.back-list {
                    font-size: 14px;
                    width: 100px;
                    height: 40px;
                    background: #fff;
                    color: #333;
                    border: 1px solid #333;
                    border-radius: 4px;
                    transition: all 0.3s ease-in-out;
                    cursor: pointer;
                    &:hover {
                        background: #333;
                        color: #fff;
                    }
                }
            }
        }
    }
}