#sub05BoardNotice {
    #section1 {
        width: 100%;
        max-width: 1340px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 100px 20px;
            .title {
                width: 100%;
                text-align: center;
                padding-bottom: 40px;
                h2 {
                    font-size: 32px;
                    font-weight: 600;
                }
            }
            .content {
                width: 100%;
                padding: 80px 0;
                .search {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;
                    padding-bottom: 16px;
                    form {
                        display: flex;
                        align-items: center;
                        input[type="text"] {
                            width: 220px;
                            height: 40px;
                            border: 1px solid #ccc;
                            border-radius: 4px;
                            padding: 0 8px;
                            outline-offset: -1px;
                            outline: 1px solid transparent;
                            transition: all 0.3s;
                            margin: 0 8px;
                            &::placeholder {
                                color: #999;
                            }
                            &:focus {
                                outline: 1px solid #666;
                            }
                        }
                        button[type="submit"] {
                            width: 48px;
                            height: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 4px;
                            background: #333;
                            color: #fff;
                            border: 1px solid #333;
                            transition: all 0.3s;
                            cursor: pointer;
                            i {
                                font-size: 16px;
                                font-weight: 500;
                            }
                            &:hover {
                                background: #fff;
                                color: #333;
                            }
                        }
                    }
                }
                .list-box {
                    width: 100%;
                    border-top: 1px solid #333;
                    // 구분
                    .header {
                        width: 100%;
                        display: flex;
                        border-bottom: 1px solid #ccc;
                        > ul.left {
                            width: 70%;
                            display: flex;
                            padding: 20px 0;
                            li.col {
                                margin-right: 6px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                &.col1 {
                                    width: calc(8% - 6px);
                                }
                                &.col2 {
                                    width: 80px;
                                }
                                &.col3 {
                                    width: calc(92% - 6px - 80px);
                                }
                                h3 {
                                    font-size: 16px;
                                    font-weight: 500;
                                }
                            }
                        }
                        > ul.right {
                            width: 30%;
                            display: flex;
                            padding: 20px 0;
                            li.col {
                                margin-right: 6px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                &.col4 {
                                    width: calc(35% - 6px);
                                }
                                &.col5 {
                                    width: calc(35% - 6px);
                                }
                                &.col6 {
                                    width: 30%;
                                    margin-right: 0;
                                }
                                h3 {
                                    font-size: 16px;
                                    font-weight: 500;
                                }
                            }
                        }
                    }

                    // 글
                    .list {
                        width: 100%;
                        > ul {
                            width: 100%;
                            li.row {
                                width: 100%;
                                border-bottom: 1px solid #ccc;
                                display: flex;
                                &.important {
                                    background: #f9f9f9;
                                }
                                > ul.left {
                                    width: 70%;
                                    display: flex;
                                    padding: 20px 0;
                                    li.col {
                                        margin-right: 6px;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        &.col1 {
                                            width: calc(8% - 6px);
                                        }
                                        &.col2 {
                                            width: 80px;
                                            span {
                                                width: 100%;
                                                height: 28px;
                                                border-radius: 20px;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                font-size: 14px;
                                                font-weight: 500;
                                            }
                                        }
                                        &.col3 {
                                            width: calc(92% - 6px - 80px);
                                            justify-content: flex-start;
                                        }
                                        span {
                                            font-size: 16px;
                                            font-weight: 500;
                                        }
                                        a {
                                            width: 100%;
                                            font-weight: 500;
                                            display: block;
                                            white-space: nowrap;
                                            overflow: hidden;
                                            text-overflow: ellipsis;
                                            transition: all 0.3s;
                                            &:hover {
                                                color: #6671f0;
                                            }
                                        }
                                    }
                                }
                                > ul.right {
                                    width: 30%;
                                    display: flex;
                                    padding: 20px 0;
                                    li.col {
                                        margin-right: 6px;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        &.col4 {
                                            width: calc(35% - 6px);
                                        }
                                        &.col5 {
                                            width: calc(35% - 6px);
                                        }
                                        &.col6 {
                                            width: 30%;
                                            margin-right: 0;
                                        }
                                        span {
                                            font-size: 14px;
                                        }
                                    }
                                }
                            }
                        }

                    }
                    @media all and (max-width: 800px) {
                        .header {
                            display: none; // 헤더 숨김
                        }
                        .list {
                            > ul {
                                li.row {
                                    flex-direction: column;

                                    > ul.left {
                                        width: 100%;
                                        display: flex;
                                        padding: 16px 0 8px 0;
                                        li.col {
                                            &.col1 { display: none; }
                                            &.col2 { width: 80px; }
                                            &.col3 { width: calc(100% - 80px); }
                                            display: flex;
                                            align-items: center;
                                            justify-content: flex-start;
                                        }
                                    }

                                    > ul.right {
                                        width: 100%;
                                        display: flex;
                                        justify-content: flex-end;
                                        padding: 0 0 16px 0;
                                        li.col {
                                            margin-right: 16px;
                                            &.col4 {
                                                width: auto;
                                            }
                                            &.col5 {
                                                width: auto;
                                            }
                                            &.col6 {
                                                width: auto;
                                            }
                                            span {
                                                display: inline-block;
                                                font-size: 12px;
                                                color: var(--grayscale-60);
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .pagenation {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                button {
                    width: 32px;
                    height: 32px;
                    background: #fff;
                    border-radius: 50%;
                    border: none;
                    color: #999;
                    margin: 0 4px;
                    cursor: pointer;
                    transition: all 0.3s;
                    &:hover {
                        background: #333;
                        color: #fff;
                    }
                    &.on {
                        background: #333;
                        color: #fff;
                    }
                }
            }
        }
    }
}