#sub01ArchiveProductionDetail {
    display: flex;
    width: 100%;
    margin-top: 128px;
    .container {
        width: 100%;
        max-width: 1200px;
        margin: auto;
        padding: 80px 20px;
        #section1 {
            width: 100%;
            height: 600px;
            .thumbnail {
                width: 100%;
                display: flex;
                .left {
                    width: calc(100% / 2);
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    img {
                        width: auto;
                        height: 600px;
                        object-fit: cover;
                    }
                }
                .right {
                    width: calc(100% / 2);
                    height: 600px;
                    .info {
                        width: 100%;
                        height: calc(100% - 100px);
                        > ul {
                            > li {
                                display: flex;
                                padding-bottom: 32px;
                                h2 {
                                    font-size: 20px;
                                    font-weight: 600;
                                    margin-right: 8px;
                                }
                                span {
                                    padding: 6px 12px;
                                    color: #fff;
                                    font-size: 12px;
                                    font-weight: 600;
                                    border-radius: 4px;
                                    &.ongoing {
                                        background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                                    }
                                    &.upcoming {
                                        background: #6671f0;
                                    }
                                    &.ended {
                                        background: rgb(133, 93, 103);
                                    }
                                }
                                h3 {
                                    width: 100px;
                                    font-size: 16px;
                                }
                                h4 {
                                    font-size: 16px;
                                }
                                &.price-row {
                                    ul {
                                        li {
                                            padding-bottom: 16px;
                                            h4 {
                                                display: flex;
                                                align-items: center;
                                                justify-content: flex-start;
                                                strong {
                                                    margin: 0 8px;
                                                    font-weight: 600;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .btn-box {
                        width: 100%;
                        height: 100px;
                        display: flex;
                        align-items: end;
                        button.wishlist {
                            width: 80px;
                            height: 56px;
                            background: #f1f1f1;
                            border-radius: 6px;
                            border: 1px solid transparent;
                            margin-right: 8px;
                            transition: all 0.3s;
                            i {
                                font-size: 20px;
                                color: #fff;
                            }
                            &.on {
                                i {
                                    color: #666;
                                }
                            }
                            &:hover {
                                border: 1px solid #666;
                                background: #fff;
                                i {
                                    color: #666;
                                }
                            }
                        }
                        a {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: calc(100% - 56px);
                            height: 56px;
                            background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                            border-radius: 6px;
                            color: #fff;
                            font-weight: 600;
                            border: 1px solid #ccc;
                            font-size: 16px;
                            transition: all 0.3s;
                            &:hover {
                                opacity: 0.5;
                            }
                        }
                        span {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: calc(100% - 56px);
                            height: 56px;
                            border-radius: 6px;
                            background: #f1f1f1;
                            color: #999;
                            font-size: 16px;
                            font-weight: 600;
                        }
                    }
                }
            }
            @media all and (max-width: 1000px) {
                .thumbnail {
                    .left {
                        width: 60%;
                    }
                    .right {
                        width: 40%;
                    }
                }
            }
            @media all and (max-width: 800px) {
                height: auto;
                .thumbnail {
                    flex-flow: column;
                    .left {
                        width: 100%;
                        align-items: center;
                        justify-content: center;
                        padding-bottom: 28px;
                        img {
                            width: 100%;
                            height: auto;
                        }
                    }
                    .right {
                        width: 100%;
                    }
                }
            }
        }   
        #section2 {
            width: 100%;
            padding-top: 100px;
            .content {
                .content-title {
                    width: 100%;
                    height: 56px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-top: 1px solid #333;
                    border-bottom: 1px solid #ccc;
                    margin-bottom: 40px;
                    h2 {
                        font-size: 16px;
                    }
                }
                .content-detail {
                    width: 100%;
                    padding-bottom: 40px;
                    border-bottom: 1px solid #333;
                    img {
                        width: 100%;
                        object-fit: cover;
                    }
                }
            }
            .list-btn {
                width: 100%;
                padding-top: 80px;
                display: flex;
                align-items: center;
                justify-content: center;
                a {
                    display: inline-block;
                    font-size: 12px;
                    font-weight: 600;
                    padding: 16px 32px;
                    background: #333;
                    color: #fff;
                    border: 1px solid transparent;
                    transition: all 0.3s;
                    border-radius: 4px;
                    &:hover {
                        border: 1px solid #333;
                        background: none;
                        color: #333;
                    }
                }
            }
        }   
    }
}