#wrap {
    #main {
        #section7 {
            width: 100%;
            padding: 50px 0;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            background: #f9f9f9;
            .container {
                width: 100%;
                height: auto;
                .content {
                    width: 100%;
                    max-width: 1170px;
                    margin: 0 auto;
                    ul {
                        width: 100%;
                        padding: 0 16px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        li {
                            height: 60px;
                            text-align: center;
                            a {
                                width: 100%;
                                height: 100%;
                                display: block;
                                transition: all 0.3s;
                                filter: grayscale(1);
                                img {
                                    width: 100%;
                                    height: 100%;
                                    object-fit: cover;
                                    opacity: 0.6;
                                }
                                &:hover {
                                    transform: translateY(-4px);
                                }
                            }
                        }
                    }
                }
                @media all and (max-width: 900px) {
                    ul {
                        flex-direction: column;
                        li {
                            padding: 12px 0;
                        }
                    }
                }
            }
            &.on {
                .container {
                    .content {
                        ul {
                            li {
                                animation: section10Li 2s forwards ease-in-out;
                            }
                            @keyframes section10Li {
                                  0% {opacity: 0;}
                                100% {opacity: 1;}
                            }
                        }
                    }
                }
            }
        }
    }
}