#sub05BoardContactUs {
    #section1 {
        width: 100%;
        max-width: 1000px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 0 20px;
            .title {
                width: 100%;
                padding: 80px 20px;
                text-align: center;
                h2 {
                    font-size: 20px;
                    font-weight: 600;
                }
            }
            .content {
                width: 100%;
                form {
                    width: 100%;
                    ul {
                        width: 100%;
                        li {
                            width: 100%;
                            padding-bottom: 24px;
                            .gap {
                                width: 100%;
                                label {
                                    width: 100%;
                                }
                                input[type="text"] {
                                    width: 100%;
                                    height: 32px;
                                    margin-top: 8px;
                                    border: 1px solid #ccc;
                                    border-radius: 4px;
                                    padding: 4px 8px;
                                    outline-offset: -1px;
                                    outline: 0;
                                    &:focus {
                                        outline: 1px solid #999;
                                    }
                                    &::placeholder {
                                        font-size: 12px;
                                        color: #999;
                                    }
                                }
                                textarea {
                                    width: 100%;
                                    margin-top: 8px;
                                    height: 300px;
                                    resize: none;
                                    border: 1px solid #ccc;
                                    border-radius: 4px;
                                    padding: 4px 8px;
                                    outline-offset: -1px;
                                    outline: 0;
                                    &:focus {
                                        outline: 1px solid #999;
                                    }
                                    &::placeholder {
                                        font-size: 12px;
                                        color: #999;
                                    }
                                }
                            }
                        }
                    }
                    .button-box {
                        width: 100%;
                        padding-top: 50px;
                        text-align: center;
                        button {
                            width: 240px;
                            height: 56px;
                            background: #333;
                            color: #fff;
                            font-size: 15px;
                            font-weight: 500;
                            border: 2px solid #333;
                            transition: all 0.3s;
                            border-radius: 3px;
                            cursor: pointer;
                            &:hover {
                                 background: #fff;
                                color: #333;
                            }
                        }
                    }
                }
            }
        }
    }
}