#wrap {
    #main {
        #section2 {
            padding: 100px 0;
            overflow: hidden;
            .container {
                width: 100%;
                margin: 0 auto;
                height: auto;
                .title {
                    text-align: center;
                    h2 {
                        display: inline-block;
                        font-size: 14px;
                        font-weight: 600;
                        padding-bottom: 20px;
                        background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                        background-clip: text;
                        background-size: cover;
                        color: transparent;
                    }
                    h3 {
                        padding-bottom: 80px;
                        font-size: 32px;
                        font-weight: 600;
                        line-height: 140%;
                    }
                }
                .content {
                    width: 100%;
                    > ul {
                        display: flex;
                        padding: 20px;
                        > li {
                            padding: 0 8px;
                            .gap {
                                width: 100%;
                                overflow: hidden;
                                position: relative;
                                img {
                                    width: 100%;
                                    position: relative;
                                    z-index: 1;
                                    transition: all 0.3s;
                                }
                                .contents {
                                    position: absolute;
                                    z-index: 2;
                                    bottom: 0;
                                    left: 0;
                                    width: 100%;
                                    background: linear-gradient(to top, rgba(19, 21, 39, 1), rgba(19, 21, 39, 0.5), rgba(19, 21, 39, 0));
                                    > ul {
                                        padding: 40px;
                                        > li {
                                            padding-bottom: 8px;
                                            &:last-child {
                                                padding-bottom: 0;
                                            }
                                            h3 {
                                                background: #6671f0;
                                                padding: 6px 12px;
                                                display: inline-flex;
                                                font-size: 12px;
                                                color: #fff;
                                                font-weight: 400;
                                                border-radius: 2px;
                                                margin: 0;
                                            }
                                            h4 {
                                                font-size: 24px;
                                                color: #fff;
                                                font-weight: 500;
                                            }
                                            p {
                                                font-size: 16px;
                                                color: #fff;
                                                font-weight: 500;
                                                line-height: 140%;
                                                padding: 8px 0;
                                            }
                                            a {
                                                display: inline-flex;
                                                align-items: center;
                                                justify-content: center;
                                                color: #333;
                                                font-size: 11px;
                                                font-weight: 600;
                                                background: #fff;
                                                border: 1px solid transparent;
                                                width: 120px;
                                                height: 32px;
                                                transition: all 0.3s;
                                                &:hover {
                                                    background: none;
                                                    border: 1px solid #fff;
                                                    color: #fff;
                                                }
                                            }
                                        }
                                    }
                                    @media all and (max-width: 420px) {
                                        > ul {
                                            padding: 16px;
                                            > li {
                                                h3 {
                                                    font-size: 8px;
                                                }
                                                h4 {font-size: 14px;}
                                                a {
                                                    font-size: 9px;
                                                    width: 80px;
                                                    height: 28px;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            &:hover {
                                .gap {
                                    img {
                                        transform: scale(1.2);
                                    }
                                }
                            }
                        }
                        @media all and (max-width: 1400px) {
                            flex-flow: row wrap;
                            > li {
                                width: calc(100% / 2);
                                &:nth-child(1),
                                &:nth-child(2) {
                                    padding-bottom: 16px;
                                }
                            }
                        }
                        @media all and (max-width: 780px) {
                            flex-flow: row wrap;
                            > li {
                                width: 100%;
                                padding-bottom: 16px;
                                &:last-child {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }
            @media all and (max-width: 480px) {
                .container {
                    .title {
                        h2 {
                            font-size: 12px;
                            padding-bottom: 16px;
                        }
                        h3 {
                            font-size: 20px;
                            line-height: 140%;
                            padding-bottom: 16px;
                        }
                        a {
                            font-size: 10px;
                            padding: 8px 16px;
                        }
                    }
                }
            }
            &.on {
                .title {
                    animation: section3Title 2s forwards ease-in-out;
                }
                @keyframes section3Title {
                        0% {opacity: 0;}
                    100% {opacity: 1;}
                }
                .content {
                    > ul {
                        > li {
                            &:nth-child(1){
                                animation: section3Nth1 2s forwards ease-in-out;
                            }
                            &:nth-child(2){
                                animation: section3Nth2 2.2s forwards ease-in-out;
                            }
                            &:nth-child(3){
                                animation: section3Nth3 2.4s forwards ease-in-out;
                            }
                            &:nth-child(4){
                                animation: section3Nth4 2.6s forwards ease-in-out;
                            }
                            @keyframes section3Nth1 {
                                    0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section3Nth2 {
                                    0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section3Nth3 {
                                    0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                            @keyframes section3Nth4 {
                                    0% {transform: translateY(1000px);}
                                100% {transform: translateY(   0px);}
                            }
                        }
                    }
                }
            }
        }
    }
}