#wrap {
    #main {
        #section1 {
            width: 100%;
            height: calc(100px - 128px);
            margin-top: 128px;
            .slide-container {
                width: 100%;
                height: 100%;
                position: relative;
                .slide-view {
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    position: relative;
                    z-index: 1;
                    .slide-wrap {
                        display: flex;
                        width: 100% * 5;
                        height: 100%;
                        margin-left: -100%;
                        position: relative;
                        left: -100% * 0;
                        .slide {
                            width: 100%;
                            height: 100%;
                            .gap {
                                width: 100%;
                                position: relative;
                                img {
                                    width: 100%;
                                    height: 100%;
                                    object-fit: cover;
                                    position: relative;
                                    z-index: 1;
                                }
                                .title {
                                    position: absolute;
                                    z-index: 2;
                                    top: 0;
                                    left: 0;
                                    width: 100%;
                                    height: 100%;
                                    background: linear-gradient(to right, rgba(19, 21, 39, 1), rgba(19, 21, 39, 0));
                                    .title-container {
                                        width: 100%;
                                        max-width: 1400px;
                                        height: 100%;
                                        margin: auto;
                                        display: flex;
                                        align-items: center;
                                        justify-content: flex-start;
                                        ul {
                                            li {
                                                h2 {
                                                    color: #ccc;
                                                    font-size: 16px;
                                                    font-weight: 300;
                                                    white-space: pre-wrap;
                                                }
                                                h3 {
                                                    color: #fff;
                                                    font-size: 72px;
                                                    font-weight: 600;
                                                    padding: 20px 0 40px 0;
                                                    white-space: pre-wrap;
                                                }
                                                a {
                                                    color: #fff;
                                                    display: flex;
                                                    align-items: center;
                                                    justify-content: center;
                                                    width: 120px;
                                                    height: 40px;
                                                    font-size: 12px;
                                                    font-weight: 600;
                                                    background: linear-gradient(to right, rgb(102, 113, 240), rgb(164, 116, 202), rgba(240, 119, 152));
                                                    transition: all 0.3s ease-in-out;
                                                    &:hover {
                                                        background: #fff;
                                                        color: #333;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            @media all and (max-width: 1400px) {
                                .gap {
                                    .title {
                                        .title-container {
                                            margin-left: 100px;
                                        }
                                    }
                                }
                            }
                            @media all and (max-width: 800px) {
                                .gap {
                                    .title {
                                        .title-container {
                                            justify-content: center;
                                            text-align: center;
                                            margin-left: 0;
                                            ul {
                                                display: flex;
                                                flex-direction: column;
                                                align-items: center;
                                                justify-content: center;
                                                li {
                                                    h3 {
                                                        padding: 16px 0 32px 0;
                                                        font-size: 48px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            @media all and (max-width: 500px) {
                                .gap {
                                    .title {
                                        display: none;
                                    }
                                }
                            }
                        }
                    }
                }
                .count-box {
                    position: absolute;
                    z-index: 2;
                    top: 50%;
                    &.prev-box {left: 40px;}
                    &.next-box {right: 40px;}
                    .count-btn {
                        i {
                            font-size: 28px;
                            font-weight: 600;
                            color: #808080;
                            &:hover {
                                color: #3452ff;
                            }
                        }
                    }
                    @media all and (max-width: 1140px) {
                        &.prev-box {left: 10px;}
                        &.next-box {right: 10px;}
                    }
                }
            }
        }
    }
}