#wrap {
    #main {
        #section3 {
            width: 100%;
            padding: 100px 0;
            overflow: hidden;
            .container {
                width: 100%;
                max-width: 1170px;
                height: auto;
                margin: 0 auto;
                .title {
                    width: 100%;
                    padding-bottom: 80px;
                    .tab-box {
                        width: 100%;
                        text-align: center;
                        button.tab-btn {
                            width: 25%;
                            border: 0;
                            background: none;
                            span {
                                display: block;
                                font-size: 16px;
                                font-weight: 600;
                                color: #808080;
                                padding-bottom: 8px;
                                position: relative;
                                text-transform: uppercase;
                                overflow: hidden;
                                transition: all 0.3s;
                                &::after {
                                    content: '';
                                    position: absolute;
                                    bottom: 0;
                                    left: 100%;
                                    display: block;
                                    width: 100%;
                                    height: 1px;
                                    background: #333;
                                }
                            }
                            &:hover {
                                span {
                                    color: #333;
                                    &::after {
                                        animation: ani-1 0.3s forwards ease-in-out;
                                    }
                                    @keyframes ani-1 {
                                          0% {left: -100%;}
                                        100% {left:    0%;}
                                    }
                                }
                            }
                            &.active {
                                span {
                                    color: #333;
                                    &::after {
                                        left: 0;
                                    }
                                }
                            }
                        }
                    }
                    @media all and (max-width: 1040px) {
                        .tab-box {
                            button.tab-btn {
                                width: 100%;
                                padding-bottom: 16px;
                            }
                        }
                    }
                    @media all and (max-width: 480px) {
                        padding-bottom: 24px;
                        .tab-box {
                            button.tab-btn {
                                span {
                                    font-size: 14px;
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }
                .content {
                    width: 100%;
                    .text-box {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 0 16px;
                        display: none;
                        &.book-box.active {display: flex;}
                        &.poster-box.active {display: flex;}
                        &.sticker-box.active {display: flex;}
                        &.badge-box.active {display: flex;}
                        img {
                            width: 50%;
                        }
                        .section3-text-box {
                            padding-left: 100px;
                            ul {
                                li {
                                    h2 {
                                        font-size: 14px;
                                        font-weight: 600;
                                        padding-bottom: 20px;
                                        background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                                        background-clip: text;
                                        background-size: cover;
                                        color: transparent;
                                    }
                                    h3 {
                                        font-size: 32px;
                                        font-weight: 600;
                                        padding-bottom: 40px;
                                        white-space: pre-wrap;
                                    }
                                    p {
                                        font-size: 15px;
                                        font-weight: 400;
                                        line-height: 200%;
                                        color: #808080;
                                        padding-bottom: 40px;
                                    }
                                    a {
                                        display: inline-block;
                                        font-size: 12px;
                                        font-weight: 600;
                                        padding: 16px 32px;
                                        background: #333;
                                        color: #fff;
                                        border: 1px solid transparent;
                                        transition: all 0.3s;
                                        &:hover {
                                            border: 1px solid #333;
                                            background: none;
                                            color: #333;
                                        }
                                    }
                                }
                            }
                            @media all and (max-width: 1000px) {
                                padding-left: 40px;
                                ul {
                                    li {
                                        h2 {
                                            padding-bottom: 14px;
                                        }
                                        h3 {
                                            padding-bottom: 24px;
                                        }
                                        p {
                                            font-size: 14px;
                                            padding-bottom: 28px;
                                        }
                                        a {
                                            font-size: 12px;
                                            padding: 12px 20px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    @media all and (max-width: 780px) {
                        .text-box {
                            flex-flow: row wrap;
                            justify-content: left;
                            img {
                                width: 100%;
                            }
                            .section3-text-box {
                                padding-top: 24px;
                                padding-left: 0;
                            }
                        }
                    }
                    @media all and (max-width: 480px) {
                        .text-box {
                            flex-flow: row wrap;
                            justify-content: left;
                            img {
                                width: 100%;
                            }
                            .section3-text-box {
                                padding-top: 24px;
                                padding-left: 0;
                                ul {
                                    li {
                                        h2 {
                                            font-size: 12px;
                                        }
                                        h3 {
                                            font-size: 20px;
                                        }
                                        p {
                                            font-size: 14px;
                                        }
                                        a {
                                            font-size: 10px;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            &.on {
                .title {
                    .tab-box {
                        button.book-btn{
                            animation: section3Book 1.6s forwards ease-in-out;
                        }
                        button.poster-btn{
                            animation: section3Poster 2.0s forwards ease-in-out;
                        }
                        button.sticker-btn{
                            animation: section3Sticker 2.4s forwards ease-in-out;
                        }
                        button.badge-btn{
                            animation: section3Badge 2.6s forwards ease-in-out;
                        }
                        @keyframes section3Book {
                              0% {opacity: 0;}
                            100% {opacity: 1;}
                        }
                        @keyframes section3Poster {
                              0% {opacity: 0;}
                            100% {opacity: 1;}
                        }
                        @keyframes section3Sticker {
                              0% {opacity: 0;}
                            100% {opacity: 1;}
                        }
                        @keyframes section3Badge {
                              0% {opacity: 0;}
                            100% {opacity: 1;}
                        }
                    }
                }
                .content {
                    .text-box {
                        img {
                            animation: section3Img 1.6s forwards ease-in-out;
                        }
                        .section3-text-box {
                            animation: section3Text 1.6s forwards ease-in-out;
                        }
                        @keyframes section3Img {
                              0% {transform: translateX(-1000px);}
                            100% {transform: translateX(    0px);}
                        }
                        @keyframes section3Text {
                              0% {transform: translateX(1000px);}
                            100% {transform: translateX(   0px);}
                        }
                    }
                }
            }
        }
    }
}