#wrap {
    #footer {
        position: relative;
        z-index: 2;
        width: 100%;
        padding: 48px 0;
        background-color: #f1f1f1;
        .container {
            width: 100%;
            max-width: 1170px;
            margin: 0 auto;
            height: auto;
            ul {
                width: 100%;
                li {
                    text-align: center;
                    h1 {
                        font-size: 18px;
                        font-weight: 500;
                        color: #1d1d1d;
                    }
                    p {
                        font-size: 14px;
                        line-height: 140%;
                        color: #aaa;
                        margin: 16px 0;
                    }
                    span {
                        font-size: 12px;
                        color: #999;
                    }
                }
            }
            @media all and (max-width: 480px) {
                ul {
                    li {
                        h1 {
                            font-size: 14px;
                        }
                        p {
                            font-size: 12px;
                            margin: 16px 0;
                        }
                        span {
                            font-size: 10px;
                        }
                    }
                }
            }
        }
    }
}