#sub02LoungeTalkDetail {
    #section1 {
        width: 100%;
        max-width: 1340px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 100px 20px;

            // 타이틀
            .title {
                width: 100%;
                border-top: 1px solid #777;
                padding: 20px 40px;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                h2 {
                    width: 60px;
                    color: #6671f0;
                    font-size: 16px;
                    font-weight: 500;
                }
                h3 {
                    width: calc(100% - 60px);
                    padding: 0 16px;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 160%;
                }
                @media all and (max-width: 600px) {
                    padding: 20px;
                    flex-direction: column;
                    align-items: flex-start;
                    h3 {
                        padding: 8px 0 0 0;
                    }
                }
            }

            // 내용
            .content {
                width: 100%;
                padding: 40px;
                background: #f9f9f9;
                p {
                    font-size: 14px;
                    color: #333;
                    line-height: 160%;
                    white-space: pre-wrap;
                    line-height: 160%;
                    margin-top: 16px;
                }
                span {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    padding-bottom: 16px;
                    strong {
                        color: #777;
                        margin-right: 16px;
                    }
                    em {
                        color: #777;
                    }
                }
                @media all and (max-width: 600px) {
                    span {
                        flex-direction: column;
                        align-items: flex-end;
                        strong {
                            margin-right: 0;
                            margin-bottom: 8px;
                        }
                    }
                }
            }

            // 댓글
            .comment-box {
                padding-top: 80px;
                .comment-title {
                    width: 100%;
                    h3 {
                        font-size: 18px;
                    }
                }
                .input-box {
                    width: 100%;
                    padding: 40px 0;
                    input[type='text'] {
                        width: calc(100% - 80px - 8px);
                        margin-right: 8px;
                        height: 48px;
                        padding: 0 16px;
                        border: 1px solid #d9d9d9;
                        border-radius: 4px;
                        outline-offset: -1px;
                        outline: 1px solid transparent;
                        &::placeholder {
                            color: #999;
                        }
                        &:focus {
                            outline: 1px solid #333;
                        }
                    }
                    button.submit-btn {
                        width: 80px;
                        height: 48px;
                        border: 1px solid #333;
                        background: #fff;
                        color: #333;
                        border-radius: 4px;
                        transition: all 0.3s ease-in-out;
                        cursor: pointer;
                        &:hover {
                            background: #333;
                            color: #fff;
                        }
                    }
                }
                .comment-content {
                    > ul {
                        border-top: 1px solid #333;
                        > li {
                            border-bottom: 1px solid #d9d9d9;
                            padding: 16px;
                            > ul {
                                > li {
                                    padding: 4px 0;
                                    h3 {
                                        font-size: 14px;
                                        font-weight: 500;
                                    }
                                    p {
                                        font-size: 14px;
                                        line-height: 160%;
                                    }
                                    button {
                                        border: 0;
                                        background: none;
                                        font-size: 12px;
                                        color: #333;
                                        transition: all 0.3s ease-in-out;
                                        cursor: pointer;
                                        &:hover {
                                            color: #c33;
                                        }
                                    }
                                    i {
                                        font-size: 12px;
                                        margin: 0 4px;
                                        color: #ccc;
                                    }
                                }
                            }
                        }
                    }
                    p.empty {
                        width: 100%;
                        text-align: center;
                        padding: 40px 0;
                    }
                }
            }

            // 페이지네이션
            .pagenation {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                padding-top: 40px;
                button {
                    width: 32px;
                    height: 32px;
                    background: #fff;
                    border-radius: 50%;
                    border: none;
                    color: #999;
                    margin: 0 4px;
                    cursor: pointer;
                    transition: all 0.3s;
                    &:hover {
                        background: #333;
                        color: #fff;
                    }
                    &.on {
                        background: #333;
                        color: #fff;
                    }
                }
            }

            // 목록
            .back-btn-box {
                width: 100%;
                padding-top: 80px;
                text-align: center;
                button.back-list {
                    font-size: 14px;
                    width: 100px;
                    height: 40px;
                    background: #fff;
                    color: #333;
                    border: 1px solid #333;
                    border-radius: 4px;
                    transition: all 0.3s ease-in-out;
                    cursor: pointer;
                    &:hover {
                        background: #333;
                        color: #fff;
                    }
                }
            }
        }
    }
}