#sub10WishList {
    #section1 {
        width: 100%;
        max-width: 1340px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 0 20px;
            .title {
                width: 100%;
                padding: 80px 20px;
                text-align: center;
                h2 {
                    font-size: 20px;
                    font-weight: 600;
                }
            }
            .content {
                width: 100%;
                span {
                    width: 100%;
                    display: block;
                    text-align: center;
                    padding: 150px 0;
                    font-size: 16px;
                }
                > ul {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: flex-start;
                    gap: 16px;
                    > li {
                        width: calc((100% / 3) - 16px);
                        .gap {
                            width: 100%;
                            overflow: hidden;
                            position: relative;
                            img {
                                width: 100%;
                                position: relative;
                                z-index: 1;
                                transition: all 0.3s;
                            }
                            .contents {
                                position: absolute;
                                z-index: 2;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                background: linear-gradient(to top, rgba(19, 21, 39, 1), rgba(19, 21, 39, 0.5), rgba(19, 21, 39, 0));
                                > ul {
                                    padding: 16px;
                                    > li {
                                        padding-bottom: 8px;
                                        &:last-child {
                                            padding-bottom: 0;
                                        }
                                        h3 {
                                            padding: 6px 12px;
                                            display: inline-flex;
                                            font-size: 12px;
                                            color: #fff;
                                            font-weight: 400;
                                            border-radius: 2px;
                                            margin: 0;
                                            &.ongoing {
                                                background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                                            }
                                            &.upcoming {
                                                background: #6671f0;
                                            }
                                            &.ended {
                                                background: rgb(133, 93, 103);
                                            }
                                        }
                                        h4 {
                                            font-size: 24px;
                                            color: #fff;
                                            font-weight: 500;
                                            line-height: 160%;
                                        }
                                        p {
                                            font-size: 16px;
                                            color: #fff;
                                            font-weight: 500;
                                            line-height: 140%;
                                            padding: 8px 0;
                                        }
                                        a {
                                            display: inline-flex;
                                            align-items: center;
                                            justify-content: center;
                                            color: #333;
                                            font-size: 11px;
                                            font-weight: 600;
                                            background: #fff;
                                            border: 1px solid transparent;
                                            width: 120px;
                                            height: 32px;
                                            transition: all 0.3s;
                                            &:hover {
                                                background: none;
                                                border: 1px solid #fff;
                                                color: #fff;
                                            }
                                        }
                                    }
                                }
                                @media all and (max-width: 420px) {
                                    > ul {
                                        padding: 16px;
                                        > li {
                                            h3 {
                                                font-size: 8px;
                                            }
                                            h4 {font-size: 14px;}
                                            a {
                                                font-size: 9px;
                                                width: 80px;
                                                height: 28px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        &:hover {
                            .gap {
                                img {
                                    transform: scale(1.2);
                                }
                            }
                        }
                    }
                    @media all and (max-width: 1400px) {
                        flex-flow: row wrap;
                        > li {
                            width: calc((100% / 2) - 16px);
                            &:nth-child(1),
                            &:nth-child(2) {
                                padding-bottom: 16px;
                            }
                        }
                    }
                    @media all and (max-width: 780px) {
                        flex-flow: row wrap;
                        > li {
                            width: 100%;
                            padding-bottom: 16px;
                            &:last-child {
                                padding-bottom: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}