#sub05BoardFAQ {
    #section1 {
        width: 100%;
        max-width: 1340px;
        margin: 128px auto 100px auto;
        .container {
            width: 100%;
            padding: 100px 20px;
            .title {
                width: 100%;
                text-align: center;
                padding-bottom: 40px;
                h2 {
                    font-size: 32px;
                    font-weight: 600;
                }
            }
            .content {
                width: 100%;
                padding: 80px 0;
                .category {
                    display: flex;
                    padding: 20px 8px;
                    border-top: 1px solid #333;
                    border-bottom: 1px solid #ccc;
                    .col {
                        overflow: hidden;
                        color: #666;
                        margin: 0 8px;
                        h3 {
                            font-size: 16px;
                            font-weight: 500;
                        }
                        &.col1 {
                            width: 100px;
                        }
                        &.col2 {
                            width: calc(100% - 100px);
                            text-align: center;
                        }
                    }
                }
                .contents {
                    ul {
                        width: 100%;
                        li {
                            width: 100%;
                            display: flex;
                            flex-flow: row wrap;
                            align-items: center;
                            padding: 20px 8px;
                            transition: height 0.3s ease-in-out;
                            border-bottom: 1px solid #ccc;
                            &.on {
                                height: auto;
                            }
                            .col {
                                display: flex;
                                align-items: center;
                                overflow: hidden;
                                margin: 0 8px;
                                &.col1 {
                                    width: 100px;
                                    h5 {
                                        font-size: 14px;
                                        font-weight: 600;
                                    }
                                }
                                &.col2 {
                                    width: calc(100% - 100px - 16px - 16px);
                                    display: flex;
                                    justify-content: space-between;
                                    h4 {
                                        color: #333;
                                        font-size: 16px;
                                        line-height: 160%;
                                        margin-right: 8px;
                                        &:hover {
                                            color: #e63333;
                                        }
                                        strong {
                                            display: inline-block;
                                            vertical-align: 1px;
                                            padding-right: 8px;
                                        }
                                    }
                                    i {
                                        font-size: 20px;
                                    }
                                }

                            }

                            
                            .FaqContentSlide {
                                width: 100%;
                                height: auto;
                                padding: 30px;
                                display: flex;
                                align-items: center;
                                margin-top: 20px;
                                background: #f1f1f1;
                                .answer {
                                    span {
                                        font-size: 18px;
                                    }
                                    h5 {
                                        padding-top: 15px;
                                        font-size: 14px;
                                        color: #333;
                                        line-height: 180%;
                                        white-space: pre-wrap;
                                    }
                                }
                            }
                        }
                    }
                }
                @media all and (max-width: 600px) {
                    .category {
                        display: flex;
                        padding: 20px 8px;
                        border-top: 1px solid #333;
                        border-bottom: 1px solid #ccc;
                        .col {
                            overflow: hidden;
                            text-align: center;
                            justify-content: center;
                            color: #666;
                            h3 {
                                font-size: 16px;
                                font-weight: 500;
                            }
                            &.col1 {
                                width: 60px;
                            }
                            &.col2 {
                                width: calc(100% - 60px);
                                justify-content: flex-start;
                            }
                        }
                    }
                    .contents {
                        ul {
                            li {
                                .col {
                                    &.col1 {
                                        width: 60px;
                                        justify-content: flex-start;
                                        h5 {
                                            font-size: 14px;
                                            font-weight: 600;
                                        }
                                    }
                                    &.col2 {
                                        width: calc(100% - 60px - 16px - 16px);
                                        h4 {
                                            font-size: 14px;
                                        }
                                        i {
                                            font-size: 16px;
                                        }
                                    }

                                }

                                
                                .FaqContentSlide {
                                    width: 100%;
                                    height: auto;
                                    padding: 30px;
                                    display: flex;
                                    align-items: center;
                                    margin-top: 20px;
                                    background: #f1f1f1;
                                    .answer {
                                        span {
                                            font-size: 18px;
                                        }
                                        h5 {
                                            padding-top: 15px;
                                            font-size: 14px;
                                            color: #333;
                                            line-height: 180%;
                                            white-space: pre-wrap;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .pagenation {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                button {
                    width: 32px;
                    height: 32px;
                    background: #fff;
                    border-radius: 50%;
                    border: none;
                    color: #999;
                    margin: 0 4px;
                    cursor: pointer;
                    transition: all 0.3s;
                    &:hover {
                        background: #333;
                        color: #fff;
                    }
                    &.on {
                        background: #333;
                        color: #fff;
                    }
                }
            }
        }
    }
}