#sub04EventsDetail {
    display: flex;
    width: 100%;
    margin-top: 128px;
    .container {
        width: 100%;
        max-width: 1200px;
        margin: auto;
        padding: 80px 20px;
        #section1 {
            width: 100%;
            .thumbnail {
                width: 100%;
                display: flex;
                .left {
                    width: 300px;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    margin-right: 20px;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .right {
                    width: calc(100% - 300px);
                    .info {
                        width: 100%;
                        > ul {
                            width: 100%;
                            > li {
                                width: 100%;
                                display: flex;
                                padding-bottom: 32px;
                                h2 {
                                    font-size: 20px;
                                    font-weight: 600;
                                    margin-right: 8px;
                                }
                                span {
                                    padding: 6px 12px;
                                    color: #fff;
                                    font-size: 12px;
                                    font-weight: 600;
                                    border-radius: 4px;
                                    &.ongoing {
                                        background: linear-gradient(to right, rgb(102, 113, 240), rgba(240, 119, 152));
                                    }
                                    &.upcoming {
                                        background: #6671f0;
                                    }
                                    &.ended {
                                        background: rgb(133, 93, 103);
                                    }
                                }
                                h3 {
                                    font-size: 16px;
                                    white-space: pre-wrap;
                                    line-height: 160%;
                                }
                            }
                        }
                    }
                }
            }
            @media all and (max-width: 1000px) {
                .thumbnail {
                    .left {
                        width: 60%;
                    }
                    .right {
                        width: 40%;
                    }
                }
            }
            @media all and (max-width: 800px) {
                height: auto;
                .thumbnail {
                    flex-flow: column;
                    .left {
                        width: 100%;
                        align-items: center;
                        justify-content: center;
                        padding-bottom: 28px;
                        img {
                            width: 100%;
                            height: auto;
                        }
                    }
                    .right {
                        width: 100%;
                    }
                }
            }
        }   
        #section2 {
            width: 100%;
            padding-top: 40px;
            .content {
                .content-title {
                    width: 100%;
                    height: 56px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-top: 1px solid #333;
                    border-bottom: 1px solid #ccc;
                    h2 {
                        font-size: 16px;
                    }
                }
                .content-detail {
                    width: 100%;
                    padding: 20px;
                    border-bottom: 1px solid #333;
                    p {
                        white-space: pre-wrap;
                        line-height: 200%;
                    }
                }
            }
            .list-btn {
                width: 100%;
                padding-top: 80px;
                display: flex;
                align-items: center;
                justify-content: center;
                a {
                    display: inline-block;
                    font-size: 12px;
                    font-weight: 600;
                    padding: 16px 32px;
                    background: #333;
                    color: #fff;
                    border: 1px solid transparent;
                    transition: all 0.3s;
                    border-radius: 4px;
                    &:hover {
                        border: 1px solid #333;
                        background: none;
                        color: #333;
                    }
                }
            }
        }   
    }
}