#header {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    transition: all 0.3s;
    &.on {
        top: -128px;
    }
    &.top {
        background: transparent;
        top: 0;
    }
    .row1 {
        width: 100%;
        height: 48px;
        background: #f7f7f7;
        display: flex;
        // sns
        .left {
            width: 50%;
            height: 100%;
            ul {
                width: 200px;
                height: 48px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0 4dvb;
                li {
                    width: 100%;
                    height: 100%;
                    a {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 100%;
                        height: 100%;
                        img {
                            width: 20px;
                            height: 20px;
                            object-fit: cover;
                            transition: all 0.3s;
                        }
                        &:hover {
                            opacity: 0.8;
                        }
                    }
                }
            }
        }
        // 회원가입&로그인
        .right {
            width: 50%;
            height: 100%;
            .container {
                width: 100%;
                height: 100%;
                padding: 0 40px;
                display: flex;
                align-items: center;
                justify-content: right;
                .content {
                    display: flex;
                    align-items: center;
                    i {
                        font-size: 12px;
                        color: #999;
                        margin: 6px;
                    }
                    a {
                        font-size: 12px;
                        color: #999;
                        transition: all 0.3s;
                        &:hover {
                            color: #e63333;
                        }
                    }
                }
            }
        }
    }
    .row2 {
        width: 100%;
        height: 80px;
        background: #fff;
        padding: 0 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        // 로고
        .left {
            h1 {
                a {
                    transition: all 0.3s ease-in-out;
                    img {
                        width: auto;
                    }
                    &:hover {
                        opacity: 0.5;
                    }
                }
            }
        }
        // GNB
        .center {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            #gnb {
                height: 100%;
                > ul {
                    height: 100%;
                    display: flex;
                    > li {
                        height: 100%;
                        padding: 0 24px;
                        position: relative;
                        // 메인버튼
                        > a {    
                            display: flex;
                            height: 100%;   
                            align-items: center; 
                            justify-content: center;                    
                            span {
                                overflow: hidden;
                                width:100%;
                                display: block;
                                font-size: 13px;
                                font-weight: 500;
                                color: #232323;
                                padding-bottom: 4px;
                                position: relative;
                                &::after {
                                    content: '';
                                    position: absolute;
                                    left: -100%;
                                    bottom: 0;
                                    display: block;
                                    width: 100%;
                                    height: 1px;
                                    background: #000;
                                    transition: left 0.3s ease;
                                }
                            }  
                            &:hover {
                                span {
                                    &::after {
                                        left: 0;
                                    }
                                }
                            }                         
                            &.on {
                                span {
                                    &::after {
                                        left: 0;
                                    }
                                }
                            }
                        }
                        // 서브메뉴
                        .sub {
                            position: absolute;
                            top: 80px;
                            left: 0;
                            width: 160px;
                            height: 0;
                            opacity: 0;
                            overflow: hidden;
                            background: #333;
                            border-radius: 8px;
                            transition: all 0s ease-in-out;
                            &.on {
                                transition: all 0.3s ease-in-out;
                                height: auto;
                                opacity: 1;
                            }
                            .container {
                                width: 100%;
                                margin: 0 auto;
                                > ul {
                                    width: 100%;
                                    > li {
                                        width: 100%;
                                        > a {
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            width: 100%;
                                            transition: all 0.3s;
                                            > em {
                                                width: 100%;
                                                font-size: 13px;
                                                font-weight: 500;
                                                color: #fff;
                                                padding: 20px;
                                                display: block;
                                                transition: all 0.3s;
                                            }
                                            &:hover {
                                                background: #fff;
                                                > em {
                                                    color: #333;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .mobile-btn {
                display: none;
                width: 16px;
                height: 14px;
                position: relative;
                z-index: 3;
                i {
                    width: 100%;
                    height: 2px;
                    background: rgba(0,0,0, 1);
                    position: absolute;
                    left: 0;
                    transition: all 0.3s ease-in-out;
                    &:nth-child(1){
                        top: 0;
                    }
                    &:nth-child(2){
                        top: 50%;
                        margin-top: -1px;
                    }
                    &:nth-child(3){
                        bottom: 0;
                    }
                }

                &.on {
                    i {
                        &:nth-child(1){
                            top: 50%;
                            margin-top: -1px;
                            transform: rotate(45deg);
                        }
                        &:nth-child(2){
                            display: none;
                        }
                        &:nth-child(3){
                            bottom: 50%;
                            margin-bottom: -1px;
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
        }
        // aside
        .right {
            height: 100%;
            #aside {
                height: 100%;
                ul {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    li {
                        margin: 0 8px;
                        a {
                            i {
                                color: #333;
                                font-size: 16px;
                                transition: all 0.3s ease-in-out;
                            }
                            &:hover {
                                i {
                                    color: #e63333;
                                }
                            }
                        }
                    } 
                }
            }
        }
    }
}
@media all and (max-width: 1140px) {
    #header {
        .row2 {
            .center {
                #gnb {
                    > ul {
                        > li {
                            padding: 0 16px;
                        }
                    }
                }
            }
        }
    }
}
@media all and (max-width: 991px) {
    #header {
        .row2 {
            padding: 16px;
            .left {
                h1 {
                    a {
                        img {
                            width: 80%;
                        }
                    }
                }
            }

            .center {
                justify-content: flex-end;
                #gnb {
                    position: absolute;
                    top: 128px;
                    right: 0;
                    width: 100%;
                    height: 0;
                    background: rgba(0,0,0,0.1);
                    transition: height 0s ease-in-out;
                    overflow-y: auto;
                    > ul {
                        height: auto;
                        display: flex;
                        flex-flow: row wrap;
                        > li {
                            width: 100%;
                            height: auto;
                            padding: 0 20px;
                            padding: 0;
                            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                            > a {
                                display: flex;
                                width: 100%;
                                height: 100%;   
                                height: 50px;   
                                align-items: center; 
                                justify-content: center;
                                background: rgba(0,0,0, 1);                 
                                padding: 0 20px;
                                span {
                                    overflow: hidden;
                                    width:100%;
                                    display: block;
                                    font-size: 13px;
                                    font-weight: 500;
                                    color: #232323;
                                    color: #fff;
                                    padding-bottom: 4px;
                                    position: relative;
                                    text-transform: uppercase;
                                    &::after {
                                        content: '';
                                        position: absolute;
                                        left: 0;
                                        bottom: 0;
                                        display: block;
                                        width: 100%;
                                        height: 1px;
                                        background: #000;
                                        animation: ani-1 0.6s forwards; /* 마우스 아웃하면 우측으로 부드럽게 사라짐 */
                                    }
                                    @keyframes ani-1 {
                                          0% {left:   0%}
                                        100% {left: 100%}  /* 우측으로 사라짐 */
                                    }
                                }
                                &:hover {
                                    span {
                                        &::after {
                                            animation: ani-2 0.3s forwards;
                                        }
                                        @keyframes ani-2 {
                                              0% {left:-100%} 
                                            100% {left:   0%} /* 좌측에서 부드럽게 나타남 */
                                        }
                                    }
                                }                         
                                &.on { // 메인버튼 효과 이벤트 
                                    span {
                                        &::after {
                                            animation: ani-2 0.3s forwards;
                                        }
                                        @keyframes ani-2 {
                                              0% {left:-100%} 
                                            100% {left:   0%} /* 좌측에서 부드럽게 나타남 */
                                        }
                                    }
                                }
                            }
                            .sub {
                                position: relative;
                                top: 0;
                                left: 0;
                                width: 100%;
                                background: #141414;
                                height: 0;
                                opacity: 1;
                                border-radius: 0;
                                overflow: hidden;
                                transition: all 0s ease-in-out;
                                &.on {
                                    transition: all 0.3s ease-in-out;
                                    height: auto;
                                }
                                .container {
                                    width: 100%;
                                    max-width: calc(15px + 1140px + 15px);
                                    margin: 0 auto;
                                }
                            }
                        }
                    }
                    &.on {
                        height: auto;
                    }
                }
                .mobile-btn {
                    display: block;
                }
            }

            .right {
                display: none;
            }
        }
    }
}