#wrap {
    #notPage404 {
        z-index: 2;
        width: 100%;
        height: 100%;
        margin-top: 128px;
        .container {
            width: 100%;
            max-height: 1000px;
            .content {
                position: relative;
                width: 100%;
                height: 100%;
                .gap {
                    width: 100%;
                    height: 100%;
                    position: relative;
                    z-index: 1;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        filter: grayscale(1);
                    }
                }
                .title {
                    position: absolute;
                    z-index: 2;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-flow: column;
                    align-items: center;
                    justify-content: center;
                    h2 {
                        color: #fff;
                        font-size: 32px;
                        font-weight: 500;
                        padding-bottom: 24px;
                    }
                    p {
                        color: #fff;
                        font-size: 24px;
                        font-weight: 500;
                        padding-bottom: 24px;
                    }
                    a {
                        border: 1px solid transparent;
                        background: #1d1d1d;
                        color: #fff;
                        width: 200px;
                        height: 48px;
                        font-size: 16px;
                        font-weight: 500;
                        border-radius: 4px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        transition: all 0.3s ease-in-out;
                        &:hover {
                            border: 1px solid #1d1d1d;
                            background: #ccc;
                            color: #1d1d1d;
                        }
                    }
                }
            }
            @media all and (max-width: 780px) {
                .content {
                    .title {
                        h2 {
                            font-size: 28px;
                        }
                        p {
                            font-size: 18px;
                        }
                        a {
                            width: 160px;
                            font-size: 14px;
                        }
                    }
                }
            }
            @media all and (max-width: 480px) {
                .content {
                    .title {
                        h2 {
                            font-size: 24px;
                            padding-bottom: 20px;
                        }
                        p {
                            font-size: 14px;
                            padding-bottom: 20px;
                        }
                        a {
                            width: 140px;
                            height: 40px;
                            font-size: 12px;
                        }
                    }
                }
            }
            @media all and (max-width: 380px) {
                .content {
                    .title {
                        h2 {
                            font-size: 16px;
                            padding-bottom: 16px;
                        }
                        p {
                            font-size: 12px;
                            padding-bottom: 16px;
                        }
                        a {
                            background: #fff;
                            color: #333;
                            width: 140px;
                            height: 32px;
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}